import cx from "classnames";
import React, { CSSProperties, memo, useRef } from "react";

import { Cell, ContentType, Page as PageType, State, TableContent } from "../../types";

type Props = Readonly<{
  activeBlockId: string | null;
  content: State["content"];
  onSelectBlock: (id: string, text: string) => void;
  page: PageType;
}>;

const NULL_CELL: CSSProperties = {
  bottom: 0,
  left: 0,
  pointerEvents: "none",
  right: 0,
  top: 0,
  zIndex: 1199,
};

export const Page = memo(({ activeBlockId, content, onSelectBlock, page }: Props) => {
  const pageRef = useRef<HTMLDivElement>(null);
  let contents = null;
  let height = NaN;
  if (pageRef.current) {
    const scale = pageRef.current.clientWidth / page.width;
    height = pageRef.current.clientHeight || NaN;
    contents = page.blocks.map(b => {
      const zoneId = `zone-${b.contentId}`;
      const style = {
        height: b.height * scale,
        left: b.x * scale,
        top: b.y * scale,
        width: b.width * scale,
      };
      const selected = !!activeBlockId && b.contentId === activeBlockId;
      const parentContent = content[b.contentId];
      if (!parentContent) {
        return null;
      }
      switch (b.type) {
        case ContentType.Table: {
          const tableContent = parentContent as TableContent;
          const cells: Array<Cell> = b.rows.reduce(
            (prev, next) => prev.concat(next.cells),
            [] as Array<Cell>,
          );
          return (
            <React.Fragment key={zoneId}>
              <div
                className={cx("zone", { selected })}
                id={zoneId}
                onClick={e => {
                  e.preventDefault();
                  onSelectBlock(b.contentId, tableContent.text);
                }}
                style={style}
              />
              {cells.map(cell => {
                const cellSelected = !!activeBlockId && activeBlockId === cell.id;
                if (isNaN(cell.height) || isNaN(cell.width) || isNaN(cell.x) || isNaN(cell.y)) {
                  return (
                    <div
                      className={cx("zone", "zone-cell", { selected: cellSelected })}
                      key={cell.id}
                      id={`zone-${cell.id}`}
                      onClick={e => {
                        e.preventDefault();
                      }}
                      style={NULL_CELL}
                    />
                  );
                }
                return (
                  <div
                    className={cx("zone", "zone-cell", { selected: cellSelected })}
                    key={cell.id}
                    id={`zone-${cell.id}`}
                    onClick={e => {
                      e.preventDefault();
                      onSelectBlock(cell.id, cell.element.textContent || "");
                    }}
                    style={{
                      height: cell.height * scale,
                      left: cell.x * scale,
                      top: cell.y * scale,
                      width: cell.width * scale,
                    }}
                  />
                );
              })}
            </React.Fragment>
          );
        }
        default:
          return (
            <div
              className={cx("zone", { selected })}
              key={zoneId}
              id={zoneId}
              onClick={e => {
                let textContent = "";
                switch (b.type) {
                  case ContentType.Image:
                    textContent = "Image selected";
                    break;
                  case ContentType.Math:
                    textContent = "Math selected";
                    break;
                  case ContentType.Text:
                    if (parentContent.type === ContentType.Text) {
                      textContent = parentContent.text;
                    }
                    break;
                }
                e.preventDefault();
                onSelectBlock(b.contentId, textContent);
              }}
              style={style}
            />
          );
      }
    });
  }
  return (
    <div className="page-wrapper">
      <div
        className="page"
        ref={pageRef}
        style={{ height: !isNaN(height) ? `${height}px` : "auto" }}
      >
        <img src={page.backgroundSrc} />
        {contents}
      </div>
    </div>
  );
});
Page.displayName = "Page";
