import cx from "classnames";
import React, { useRef } from "react";

import { State } from "../../types";
import { useScrollToElement } from "../useScrollToElement";
import { Page } from "./Page";

type Props = Readonly<{
  activeDataPoint: State["activeDataPoint"];
  content: State["content"];
  onSelectBlock: (id: string, text: string) => void;
  pages: State["pages"];
  selectedBlock: string | null;
  viewWidth: number;
}>;

export const Pages = ({ activeDataPoint, content, onSelectBlock, pages, selectedBlock }: Props) => {
  const scrollRef = useRef<HTMLDivElement>(null);
  useScrollToElement(
    activeDataPoint,
    selectedBlock === null ? null : `zone-${selectedBlock}`,
    scrollRef,
  );
  return (
    <div className={cx("pages", { selecting: activeDataPoint.groupKey !== "" })} ref={scrollRef}>
      {pages.map((p, index) => (
        <Page
          activeBlockId={selectedBlock}
          content={content}
          key={`page-${index}`}
          onSelectBlock={onSelectBlock}
          page={p}
        />
      ))}
    </div>
  );
};

Pages.displayName = "Pages";
